.contact.section {
  padding-bottom: 6.25rem;

}

.contact__container {
grid-template-columns: 4fr 8fr;
column-gap: 1.875rem;
}

.contact__title {
  font-size: var(--h3-font-size);
  margin-bottom: 0.5rem;
}
.section__title {
  margin-bottom: 30px;
  
}
.contact__form-group {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1.5rem;
}
.contact__form-div {
  position: relative;
  margin-bottom: 1.875rem;
  height: 3.75rem;

}
.contact__form-input {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 14px;
  width: 100%;
  height: 100%;
  box-shadow: var(--shadow);
  background-color: var(--container-color);
  color: var(--text-color);
  border: none;
  outline: none;
  border-radius: 1.875rem;
  padding: 0.625rem 1.875rem;
  z-index: 1;
}

.second__title {
  text-align: center;
}
@media screen and (max-width: 1024px) {
  .contact__container {
    grid-template-columns: 300px 360px;
    justify-content: center;

  }
  .contact__form-group {
    grid-template-columns: 1fr;
  }
  .second__title {
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .contact__container {
    grid-template-columns: 310px;
  
    row-gap: 1.875rem;
  }
  .contact__info {
    text-align: center;
  }
}


@media screen and (max-width: 350px) {
  .contact__container {
    grid-template-columns: 1fr;
  

  }
  
}
