
@import url('https://fonts.googleapis.com/css2?family=Gulzar&family=Roboto:ital,wght@0,100;0,300;0,500;0,700;1,700&family=Rubik:wght@400;600;700&display=swap');

:root {
  /* Colors */
  --first-color: hsl(353, 100%, 65%);
  --title-color: hsl(244, 24%, 26%);
  --text-color: hsl(244, 16%, 43%);
  --body-color: hsl(258, 60%, 98%);
  --container-color: white;
  --aside-color: hsl(258deg 100% 97.03%);
  --purple : hsl(240deg 69.94% 66.08%);
  --green : hsl(166.53deg 64.76% 55.49%);
 
  /*Font*/
  --body-font: 'Rubik', sans-serif;
  --h1-font-size: 2.5rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;

  /*Font weight*/
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;
  /* box shadow */
  --shadow: 0px 5px 20px 0px rgba(69 67 96 / 10%);

  /* border radius */
  --border-radius: 20px;
}
  /*base padding*/
 * { 
  margin: 0;
  padding: 0; 
box-sizing: border-box;
}

@media screen and (max-width: 1024px) {
  :root {
    --h1-font-size: 1.75rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
}
}
html {
scroll-behavior: smooth;

}

body,
button,
input,
textarea {
  font-family: var(--body-font);
 
  font-size: var(--normal-font-size);

}

body {
  background-color: var(--body-color);
  color: var(--text-color);
  line-height: 1.7;
}

h1, h2, h3 {
  color: var(--title-color);
  font-weight: var(font-semibold);
  line-height: 1.2;
}

ul {
  list-style: none;

}

a {
  text-decoration: none;
}

button {
outline: none;
  border: none;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}


.container {
  max-width: 1080px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.grid {
  display: grid;
}
.section {
  padding-top: 7rem;
  padding-bottom: 7rem;
}
.section-_title {
  font-size: var(--h1-font-size);
  margin-left: 0.875rem;
  font-weight: var(--font-bold);
  position: absolute;
  margin-bottom: 3.75rem;
}
.section__title::before {
content: '';
background: url(./assets/dots-bg.svg);
height: 2.25rem;
width: 2.25rem;
position: absolute;
left: -0.875rem;
top: -0.875rem;
}

.btn {
  padding: 0.75rem 2rem;
  line-height: 1;
  border-radius: 1.875rem;
  box-shadow: 0 0 1px rgb(0 0 0 / 0%);
  border: 1px solid transparent;
  color: white;
  display: inline-block;
  background-color: var(--first-color);
  font-weight: var(--font-bold);
}

@keyframes button-push {
  50% {
    transform: scala(0.8);
  }
  100% {
    transform: scala(1);
  }
}
.btn:hover {
  animation: button-push 0.3s linear 1;

}