.work__filters {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  column-gap: 1.875rem;
  margin-bottom: 2.5rem;
}

.work__item {
  cursor: pointer;
  font-weight: var(--font-bold);

}
.section__title {
  margin-bottom: 20px;
  font-size: 27px;
  text-align: center;
  
}
.work__item:hover {
color: var(--first-color);
}
.work__container {
  grid-template-columns: repeat(3, 1fr);
  gap: 1.875rem;
}

.work__card {
  position: relative;
  overflow: hidden;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  max-width: 300px;
  max-height: 300px;
  background-color: var(--aside-color);
}

.work__thumbnail {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: .3s;
}

.work__img {
  height: 180px;
  width: 100%;
  object-fit: cover;
  transition: .3s;
}
.work__mask {
  background: #6c6ce5;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  transition: .3s;
  opacity: 0;

}

.work__card:hover .work__mask {
  opacity: 0.9;
}
.work__category {
  color: #fff;
  background-color: var(--first-color);
  border-bottom-left-radius: 0.98rem;
  border-bottom-right-radius: 0.98rem;
  position: absolute;
  top: 0;
  left: 1.5rem;
  font-size: var(--small-font-size);
  display: inline-block;
  padding: 0.19rem 0.625rem;
  transform: translateY(-40px);
  transition: .3s;
}

.work__title {
  color: white;
  font-size: var(--h3-font-size);
  margin: 0 0 0 0.98rem;
  padding: 0 1.25rem ;
  position: absolute;
  top: 1.5rem;
  transform: translateY(30px);
  transition: .3s;
  opacity: 0;
}
.work__card:hover .work__title {
  opacity: 1;
}

.work__button {
  color: #fff;
  font-size: var(--h3-font-size);
  margin: 0 0 0 0.98rem;
  display: block;
  background-color: #ffd15c;
  height: 40px;
  width: 40px;
  cursor: pointer;
  text-align: center;
  line-height: 42px;
  border-radius: 50%;
  bottom:  1.5rem ;
  left: 1.5rem;
  position: absolute;
  transition: .3s;
  opacity: 0;
}

.work__card:hover .work__button {
  opacity: 1;
}
.work__card:hover .work__title 
.work__card:hover .work__category{
  opacity: 1;
  transform: translateY(0);
}

@media screen and (max-width: 1024px) {
  .work__filters {
    justify-content: center;
  }
  .work__container {
    grid-template-columns: repeat(2, 330px);
    justify-content: center;
    row-gap: 1.875rem;
  }
  .second__title {
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .work__container {
    grid-template-columns: 310px;

  }
  .work__filters {
    row-gap: .25rem;
  }
}


@media screen and (max-width: 350px) {
  .work__container {
    grid-template-columns: 310px;
    row-gap: 1.875rem;
  }

}
.work__desc {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
}
